@font-face {
  font-family: 'MV Boli';
  src: url(/cards-memory/static/media/mvboli.a7e38223.ttf) format('truetype');
  font-style: normal;
  font-weight: normal;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'MV Boli', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background-color: #28a745;
  background-image: url(/cards-memory/static/media/BackGround.802cd3e3.png);
  background-size: cover;
  background-position-x: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-attachment: fixed;
}

.home {
  min-height: 100vh;
}

.view {
  min-height: 92vh;
}

.bg-black {
  background-color: rgba(0, 0, 0, 0.6);
}

.stroke {
  font-weight: 700;
  text-shadow: -2px -2px 1px #000, 2px 2px 1px #000, -2px 2px 1px #000, 2px -2px 1px #000;
}

.size-custome {
  font-size: 2rem;
}

.btn-custom {
  height: 50%;
  width: 95%;
}

.icon-white {
  color: white;
}

.icon-white:hover {
  color: rgb(231, 231, 231);
}

.content-card {
  min-width: 5rem;
  min-height: 12rem;
}

.card-flip {
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  cursor: pointer;
  position: relative;
  width: 100%;
  height: 100%;
}

.is-flipped {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card-face {
  width: 100%;
  height: 100%;
  position: absolute;
  /* line-height: 260px; */
  text-align: center;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.card-face-front {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.hidden {
  visibility: hidden;
}

@media screen and (min-width: 425px) {
  .content-card {
    min-width: 6rem;
    min-height: 14rem;
  }

  .size-custome {
    font-size: 2.5rem;
  }

  .size-custome-mini {
    padding-top: 0.5rem;
    font-size: 1.5rem;
    margin-bottom: 0;
  }
}

@media screen and (min-width: 567px) {
  .size-custome {
    font-size: 3rem;
  }

  .size-custome-mini {
    font-size: 2rem;
  }
}

@media screen and (min-width: 720px) {
  .size-custome {
    font-size: 3rem;
  }
}

@media screen and (min-width: 767px) {
  .content-card {
    min-width: 8rem;
    min-height: 16rem;
  }

  .size-custome {
    font-size: 3rem;
    padding-top: 1rem;
  }
  .size-custome-mini {
    margin-bottom: 0.5rem;
  }
}

@media screen and (min-width: 1200px) {
  .content-card {
    min-width: 10rem;
    min-height: 20rem;
  }
}
